.sign-in-grid-container {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: 0.5fr 1fr;
    grid-template-rows: 0.75fr 1fr 1fr;
    grid-template-areas: "header-box header-box" "sign-in-box show-off-image-box" "sign-in-box show-off-image-box";
}

.sign-in-box { grid-area: sign-in-box; }

.show-off-image-box { grid-area: show-off-image-box; }

.header-box { 
    grid-area: header-box; 
    font-size: 10em;
    font-family: 'Oswald', sans-serif;
}

#sign-in-background {
    display: absolute;
    padding: 10vh 10vw;
    width: 100vw;
    height: 100vh;
    background-image: linear-gradient(to right, #3CA55C, #B5AC49);
    background-image: -webkit-linear-gradient(to right, #3CA55C, #B5AC49);
}

#sign-in-container {
    background-color: rgba(255, 255, 255, 0.6);
    width: 100%;
    height: 100%;

    padding: 10% 10%;

    border-radius: 15px;
    
}

#sign-in-main-text {
    text-align: center;
    font-family: 'Oswald', sans-serif; 
    font-size: 5em;
    width: 100%;
    margin-bottom: 20%;
}

.g-sign-in-button {
    margin: 0 auto;
    width: 240px;
    height: 50px;
    background-color: #4285f4;
    color: #fff;
    border-radius: 1px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.25);
    transition: background-color .218s, border-color .218s, box-shadow .218s;
}
.g-sign-in-button:hover {
    cursor: pointer;
    -webkit-box-shadow: 0 0 3px 3px rgba(66,133,244,.3);
    box-shadow: 0 0 3px 3px rgba(66,133,244,.3);
}
.g-sign-in-button:active {
    background-color: #3367D6;
    transition: background-color 0.2s;
}
.g-sign-in-button .content-wrapper {
    height: 100%;
    width: 100%;
    border: 1px solid transparent;
}
.g-sign-in-button img {
    width: 100%;
    height: 100%;
}
.g-sign-in-button .logo-wrapper {
    padding: 10px;
    background:#fff;
    width: 48px;
    height: 100%;
    border-radius: 1px; 
    display: inline-block;
}
.g-sign-in-button .text-container {
    font-family: Roboto,arial,sans-serif;
    font-weight: 500;
    letter-spacing: .21px;
    font-size: 16px;
    line-height: 48px;
    vertical-align: top;
    border: none;
    display: inline-block;
    text-align: center;
    width: 180px;
}
  